import React from "react";
import {
  FaFileDownload,
  FaCalendarAlt,
  FaClock,
  FaMapMarkerAlt,
  FaFileAlt,
} from "react-icons/fa";
import "./ResponsiveBookingTable.css";

const ResponsiveBookingTable = ({ booking, handleDownload }) => {
  return (
    <div className="responsive-booking-container">
      <h5 className="thank-card__title mb-3">Services you have booked</h5>

      {/* Desktop Table View */}
      <div className="desktop-table-wrapper d-none d-lg-block">
        <div className="list-table">
          {/* Header */}
          <div className="list-row header">
            <div className="col text-center">Booking ID</div>
            <div className="col text-center">Order Type</div>
            <div className="col text-center">Order Name</div>
            <div className="col text-center">Centre Name</div>
            <div className="col text-center">Appointment Date</div>
            <div className="col text-center">Appointment Time</div>
            <div className="col text-center">Service Cost(₹)</div>
            <div className="col text-center">Download</div>
          </div>

          {/* Rows */}
          {booking?.services?.map((service, index) => (
            <div className="list-row" key={index}>
              <div className="col text-center">{service?.bookingId}</div>
              <div className="col text-center">{service?.type}</div>
              <div className="col text-center">{service?.name}</div>
              <div className="col text-center">
                {service?.centre?.name}
              </div>
              <div className="col text-center">{service?.appointmentDate}</div>
              <div className="col text-center">{service?.appointmentTime}</div>
              <div className="col text-center">
                ₹{service?.totalPrice}
              </div>
              <div className="col text-center">
                <button
                  className="download-btn"
                    style={{
      display: "flex",
      justifyContent: "center",
      alignItems: "center",
      margin: "0 auto"
    }}
                  onClick={() => handleDownload(service?.bookingId)}
                  aria-label={`Download invoice for booking ${service?.bookingId}`}
                >
                  <FaFileDownload />
                </button>
              </div>
            </div>
          ))}
        </div>
      </div>

      {/* Mobile Card View */}
      <div className="mobile-cards-wrapper d-block d-lg-none">
        {booking?.services?.map((service, index) => (
          <div
            className="booking-card"
            key={index}
            style={{ "--index": index }}
          >
            {/* Card Header */}
            <div className="booking-card-header">
              <div className="booking-id-section">
                <span className="booking-id-label">Booking ID</span>
                <span className="booking-id-value">#{service?.bookingId}</span>
              </div>
              <button
                className="download-btn-mobile"
                onClick={() => handleDownload(service?.bookingId)}
                aria-label={`Download invoice for booking ${service?.bookingId}`}
              >
                <FaFileDownload />
                <span>Download</span>
              </button>
            </div>

            {/* Card Body */}
            <div className="booking-card-body">
              <div className="service-name-section">
                <h6 className="service-name">{service?.name}</h6>
                <span className="service-type-badge">{service?.type}</span>
              </div>

              <div className="booking-details-grid">
                <div className="detail-item">
                  <div className="detail-icon">
                    <FaMapMarkerAlt />
                  </div>
                  <div className="detail-content">
                    <span className="detail-label">Centre</span>
                    <span className="detail-value">
                      {service?.centre?.name}
                    </span>
                  </div>
                </div>

                <div className="detail-item">
                  <div className="detail-icon">
                    <FaCalendarAlt />
                  </div>
                  <div className="detail-content">
                    <span className="detail-label">Date</span>
                    <span className="detail-value">
                      {service?.appointmentDate}
                    </span>
                  </div>
                </div>

                <div className="detail-item">
                  <div className="detail-icon">
                    <FaClock />
                  </div>
                  <div className="detail-content">
                    <span className="detail-label">Time</span>
                    <span className="detail-value">
                      {service?.appointmentTime}
                    </span>
                  </div>
                </div>

                <div className="detail-item price-item">
                  <div className="detail-content price-content">
                    <span className="detail-label">Service Cost</span>
                    <span className="detail-value price-value">
                      ₹{service?.totalPrice}
                    </span>
                  </div>
                </div>
              </div>
            </div>
          </div>
        ))}
      </div>

      {/* Total Section */}
      <div className="total-section">
        <div className="total-row">
          <span className="total-label">Total Charge</span>
          <span className="total-value">₹{booking?.totalValue}</span>
        </div>
      </div>
    </div>
  );
};

export default ResponsiveBookingTable;
