import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import ResponsiveBookingTable from "./ResponsiveBookingTable";

const CartThankYou = () => {
  const [bookingResponse, setBookingResponse] = useState(null);

  useEffect(() => {
    const storedResponse = localStorage.getItem("bookingResponse");
    if (storedResponse) {
      setBookingResponse(JSON.parse(storedResponse));
    }
  }, []);

  if (!bookingResponse) {
    return (
      <section className="cart-thank section-bg padding-y-120 position-relative z-index-1 overflow-hidden">
        <div className="container container-two text-center">
          <h2>Loading booking details...</h2>
        </div>
      </section>
    );
  }

  const handleDownload = (bookingId) => {
    window.open(
      `${process.env.REACT_APP_BACKEND_URL}/api/invoice/${bookingId}`,
      "_blank"
    );
  };

  const { booking } = bookingResponse;

  return (
    <section className="cart-thank section-bg padding-y-120 position-relative z-index-1 overflow-hidden">
      <img
        src="assets/images/gradients/thank-you-gradient.png"
        alt=""
        className="bg--gradient"
      />

      <div className="container container-two">
        <div className="row justify-content-center">
          <div className="col-lg-6 col-md-8 col-sm-10">
            <div className="cart-thank__content text-center">
              <h2 className="cart-thank__title mb-48">
                Thank you for booking !!
              </h2>
            </div>
          </div>
        </div>

        <div className="padding-t-100">
          <div className="cart-thank__box">
            <div className="row gy-4">
              <div className="col-lg-12">
                <div className="thank-card">
                  {/* Replace your old table with this responsive component */}
                  <ResponsiveBookingTable
                    booking={booking}
                    handleDownload={handleDownload}
                  />

                  {/* Back button */}
                  <div className="flx-between gap-2 mt-5">
                    <Link to="/" className="btn btn-main flx-align gap-2 pill">
                    <span className="icon line-height-1 font-20">
                        <i className="fas fa-arrow-left" />
                      </span>
                      Back To Home
                      
                    </Link>
                  </div>

                  <div style={{ marginTop: "12px" }}>
                    <p>
                      <strong>Note:</strong>
                    </p>
                    <ul style={{ listStyleType: "disc", paddingLeft: "1rem" }}>
                      <li>
                        Payment to be made at the center by showing the invoice
                        generated by the system.
                      </li>
                      <li>
                        Home consultations can be scheduled by directly calling
                        the center.
                      </li>
                      <li>
                        No cancellations are allowed after 2 hours from the time
                        of booking.
                      </li>
                    </ul>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
};

export default CartThankYou;
